<style lang="scss" scoped>
@import '@/style/base.scss';

.user_foot_wrapper{
    height: 200px;
    width: 100%;
    min-width: 1280px;
    background-color: $module_bg_color;
    display: flex;
    justify-content: flex-start;
    padding-top: 30px;
    box-sizing: border-box;
    .user_logo_wrapper{
        width: 40%;
        box-sizing: border-box;
        padding-left: 15%;
        .logo_img{
            color:$light_font_color;
            font-size: $font28;
            margin-bottom: 15px;
        }
        .icon_share{
            display: flex;
            justify-content: flex-start;
            a{
                margin-right:10px;
                img{
                    width: 25px;
                    height: 25px;
                }
            }
        }
    }
    .title_wrapper{
        width: 60%;
        display: flex;
        justify-content: flex-start;
        .title_list{
            margin-right:150px;
            font-size: $font14;
            .title{
                color:$dark_font_color;
            }
            p{
                margin-bottom: 15px;
            }
            &:last-child{
                margin-right: 0;
            }
            .title_child{
                color: $light_font_color;
                cursor: pointer;
            }
        }
    }
