/**
 * @description: 格式验证
 */

/* IdCard */
export function validIdCard(IdCard){
  const reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/; 
  return reg.test(IdCard) 
}

/* 银行卡号 */
export function vaildBankCard(bankCard){
  const reg =  /^([1-9]{1})(\d{14}|\d{18})$/;
  return reg.test(bankCard)
}

/* email */
export function vaildEmail(email){
  const reg=/^\w+@\w+(\.[a-zA-Z]{2,3}){1,2}$/;
  return reg.test(email)
}

/* phone */
export function vaildPhone(phone){
  const reg=/^1(3|4|5|7|8)\d{9}$/;
  return reg.test(phone)
}

/* password */
export function vaildPassword(password){
  // 8-20位数字字母组合
  const reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{8,20}$/
  return reg.test(password)
}

/* wxCode */
export function vaildWxCode(wxCode){
  // 5到19位数，只能数字英文 下划线
  const reg=/ ^[a-zA-Z]{1}[-_a-zA-Z0-9]{5,19}$/;
  return reg.test(wxCode)
}

/* realName */
export function vaildRealName(realName){
  const reg = /^[\u4E00-\u9FA5\uf900-\ufa2d·s]{2,20}$/
  return reg.test(realName)
}

/* 营业执照编号 */
export function vaildLicense(license){
  const reg = /(^(?:(?![IOZSV])[\dA-Z]){2}\d{6}(?:(?![IOZSV])[\dA-Z]){10}$)|(^\d{15}$)/
  return reg.test(license)
}


